* {
  box-sizing: border-box;
  outline: none !important;
  -webkit-tap-highlight-color: transparent;
  font-feature-settings: "pnum" on, "lnum" on;
}

html {
  font-size: 10px;
  line-height: 122%;
}

body {
  position: relative;
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  overflow-x: hidden;
}

.main {
  position: relative;
}

.section-title {
  font-weight: 800;
  font-size: 3.6rem;
  line-height: 4.4rem;
  letter-spacing: 0.075em;
  text-transform: uppercase;
}

.button {
  display: inline-block;
  background: linear-gradient(88.72deg, #6E2AFF 1.13%, #50ABFF 234.23%);
  border-radius: 10px;
  padding: 2rem 5rem;
  font-weight: 500;
  font-size: 1.6rem;
  line-height: 101.4%;
  text-transform: uppercase;
  color: #FFFFFF;
}

.main-container {
  max-width: 1500px;
  padding: 0 100px;
  margin: auto;
}
@media all and (max-width: 1300px) {
  .main-container {
    padding: 0 75px;
  }
}
@media all and (max-width: 1024px) {
  .main-container {
    padding: 0 50px;
  }
}
@media all and (max-width: 768px) {
  .main-container {
    padding: 0 30px;
  }
}
@media all and (max-width: 420px) {
  .main-container {
    padding: 0 20px;
  }
}

.header__pre {
  padding-top: 2.5rem;
  padding-bottom: 2.1rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header__wrapper {
  display: flex;
  align-items: center;
}
.header__search {
  display: flex;
  align-items: center;
  position: relative;
  margin-right: 3.8rem;
}
.header__search button {
  position: absolute;
  right: 0;
  top: 0;
  height: 100%;
  display: flex;
  align-items: center;
  z-index: 2;
  background-color: transparent;
  border: none;
}
.header__search input {
  background: #EFF5FE;
  border-radius: 5px;
  font-size: 1.4rem;
  line-height: 1.6rem;
  color: #627289;
  border: none;
  padding: 1.2rem 5rem 1.2rem 2rem;
}
.header__name {
  font-size: 1.4rem;
  line-height: 1.7rem;
  color: #627289;
}
.header__value a {
  font-size: 1.6rem;
  line-height: 2rem;
